.platform-ios.platform-cordova:not(.fullscreen) {
    & .item-input-favorites {
        top: 108px !important;
    }
    & .view-area-favorites {
        top: 163px !important;
    }
}

.view-area-favorites {
    top: 144px !important;
}

.item-input-favorites {
    display: flex;
    align-items: center;
    overflow: hidden;
    position: relative;
    background: $favorites-search-fields-bg;
    padding: 0.7em 1em;
    top: 88px !important;

    .icon {
        position: absolute;
        right: 1.5em;
        top: 30%;
        color: $favorites-search-icon;

        &:before {
            font-size: 1.5em;
        }
    }

    input {
        font-size: 1em;
        background: $favorites-search-bg;
        border-radius: 25px;
        color: $favorites-search-text;
        padding: 0 2.5em 0 1em !important;
        border: 1px solid $favorites-search-border;
    }
}

.favorites-item {
    display: flex;
}

.icon-right-item {
    position: absolute;
    width: 2em;
    height: 2em;
    left: auto;
    font-weight: 600;
    right: 3%;
    top: 23%;
    z-index: 1;

    i {
        position: absolute;
        top: 5%;
        right: 25%;

        &:before {
            font-size: 1.7em;
            color: $instruments-text;
        }

        &.ion-checkmark-circled {
            right: 14%;
            top: 6%;
        }

        &.ion-checkmark-circled:before {
            color: $focus-color;
        }
    }
}