.uploaddocument-page {
    label {
        cursor: pointer;
        color: $doc-v2-button-color;
        margin: 0;
        width: 100%;

        input {
            display: none;
        }
    }

    .documents-list {
        margin: 0 16px;
        box-shadow: 0 2px 2px $document-list-shadow;
        border-color: $document-list-border;
        border-radius: 2px;

        .list {
            padding-bottom: 0;
        }

        .card.item {
            background-color: $document-list-bg;
        }

        &.v2 ion-item {
            height: auto;
        }

        ion-item {
            height: 60px;
            margin: 0;
            padding: 0;
            box-shadow: none;
            border: 1px solid $document-list-border;
            border-bottom: 0;
            border-radius: 0;

            &:last-of-type {
                margin: 0;
            }

            .item-content {
                padding: 16px;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: $document-list-text;
                background-color: $document-list-bg;

                > span {
                    color: currentColor;
                }

                > i.icon {
                    color: $document-list-arrow;
                }

                .title {
                    display: flex;
                    align-items: center;
                    color: currentColor;

                    > span {
                        color: currentColor;
                    }

                    i.icon {
                        font-size: 24px;
                        margin-right: 12px;
                        color: $document-list-icon;

                        &.icon-proof_of_identity:before {
                            content: "\e92a";
                        }

                        &.icon-proof_of_residence:before {
                            content: "\e92d";
                        }

                        &.icon-proof_of_payment:before {
                            content: "\e928";
                        }

                        &.icon-document_other:before {
                            content: "\e92d";
                        }
                    }
                }
            }

            .file-title {
                color: $document-file-title;
            }

            .file-subtitle {
                color: $document-file-subtitle;
            }
        }
    }

    .upload {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        label {
            color: $document-file-icon_download;

            &:hover {
                color: $document-file-icon-hover_download;
            }
        }

        div:first-child {
            margin-right: 10px;
        }
    }
    .documents {
        .v2 {
            .row {
                padding: 0;
                justify-content: space-between;
                font-size: 12px;

                &.twoSideOption {
                    font-size: inherit;
                }
            }

            .item {
                color: $placeholder;
            }

            .item-divider {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .docStatus {
                    font-size: 13px;
                    color: $pending-color;
                    background-color: $doc-v2-pending-bg-color;
                    border-radius: 3px;
                    padding: 3px 5px;
                    display: flex;
                    align-items: center;
                    .icon {
                        font-size: 16px;
                        margin-left: 3px;
                    }
                }
            }

            .upload-button {
                display: flex;
                align-items: center;
                max-width: 48%;
                overflow: hidden;

                label {
                    border: $doc-v2-upload-border; //$document-list-border;
                    border-radius: 3px;
                    height: 50px;
                    .icon {
                        color: $doc-v2-button-color;
                    }
                    &.uploaded {
                        background-color: $doc-v2-select-bg-color;
                        border: $doc-v2-upload-border-uploaded;
                        flex-direction: column;
                        .title {
                            color: $card-color;
                        }
                        .replace {
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            font-size: 12px;
                            .icon {
                                font-size: 15px;
                            }
                        }
                    }
                }
                .row {
                    align-items: center;
                    justify-content: center;
                    background: $doc-v2-select-bg-color;
                }

                span {
                    // color: $document-list-arrow;
                }

                .col {
                    display: flex;
                }

                .icon-Upload, 
                .ion-refresh,
                .icon-Touch_UP_Icon {
                    color: $document-file-icon_download;
                    margin-right: 5px;
                }

                .icon-Touch_UP_Icon {
                    font-size: 16px;
                }

                &.disabled {
                    //opacity: 0.5;
                }
            }

            .item-input {
                .disabled {

                }
            }

            &.card {
                background-color: $content-bg;

                box-shadow: none;
                margin: 0 10px;
                .item {
                    background: transparent;
                    border: none;
                    .item-input {

                        .input-label {
                            background: $doc-v2-select-bg-color;
                            border: $doc-v2-select-border;
                            border-radius: 3px;
                            color: $card-color;
                            width: 100%;
                            height: 50px;
                            display: flex;
                            align-items: center;
                            max-width: none;
                            &.disabled {
                                background-color: $doc-v2-select-disabled-bg-color;
                            }
                            .placeholder {
                                color: $doc-v2-input-placeholder;
                            }
                        }
                        .icon {
                            position: absolute;
                            right: 10px;
                        }
                    }

                }

                .item-divider {
                    color: $card-color;
                    border: none;
                }
            }
        }
        hr {
            border: $doc-v2-hr-border;
        }
    }


    &.documents-v2 {
        .approved-pandats {
            display: flex;
            flex-direction: column;
            padding: 10px;
            align-items: center;

            .no-actions-required-icon {
                font-size: 100px;
                color: #2D9CDB;
                margin-top: 20vh;
            }
            .approved-title-pandats {
                margin-top: 10px;
                font-size: 20px;
                line-height: 24px;
                color: $doc-v2-no-action-required-title-color;
            }

            .approved-text-pandats {
                margin-top: 10px;
                color: $doc-v2-no-action-required-text-color;
                width: 240px;
            }
        }
    }
    .file-icons {
        .ion-close {
            color: $document-file-icon_close;
        }

        .ion-refresh {
            color: $document-file-icon_refresh;
        }

        .ion-checkmark {
            color: $document-file-icon_checkmark;
        }
    }

    &.upload-other {
        .bar-subheader.bar {
            background-color: $content-bg;

            & .card {
                background-color: $document-list-bg;
            }
        }

        ion-content {
            &.has-header {
                top: 140px;
            }

            &.has-footer {
                bottom: 80px;
            }
        }

        ion-list {
            ion-item.card {
                padding-left: 8px;
                padding-right: 8px;

                > .row {
                    > .col:first-child {
                        flex: 1;
                        flex-direction: column;
                        height: 100%;
                        display: flex;
                        padding-left: 0;
                        overflow: hidden;

                        > div.file-title {
                            width: 100%;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }

                    > .col:last-child {
                        max-width: 52px;
                        height: 100%;
                        display: flex;
                        min-width: 52px;
                        justify-content: flex-end;
                        padding-right: 0;

                        &.remove {
                            display: flex;
                            flex-direction: row;
                            justify-content: flex-end;

                            button {
                                display: flex;
                                background: none;
                                min-width: 42px;
                                min-height: 42px;

                                &::before {
                                    font-size: 20px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .play-video-area-pandats {
        padding: 10px 0 16px;
        margin: 30px 16px 0;

        > .layout-column {

            > .layout-row {
                + .layout-row {
                    margin-top: 20px;
                }

                &.play-video-description {
                    text-align: center;
                }

                > .layout-column {
                    border-radius: 14px;
                    box-shadow: -1px 2px 6px rgba(0, 0, 0, .3);

                    > .play-video-btn-pandats {
                        color: $document-file-title;
                        background: $option-button-bg_active;
                        border-radius: 14px;
                        //padding: 7px 14px;
                        padding: 0 7px;
                        width: 155px;
                        height: 40px;

                        text-transform: uppercase;
                        white-space: nowrap;
                        text-decoration: none;

                        &:hover {
                            text-decoration: none;
                        }

                        > * {
                            margin-left: 10px;
                        }

                        > .layout-column:first-child {
                            font-size: 16px;
                        }

                        .play-icon {
                            border: 1px solid $document-file-title;
                            width: 20px;
                            height: 20px;
                            border-radius: 10px;
                            position: relative;

                            &:after {
                                content: '';
                                position: absolute;
                                left: 6px;
                                top: 5px;
                                right: 0;
                                bottom: 5px;

                                border-left: 8px solid $document-file-title;
                                border-right: 0 solid transparent;
                                border-top: 4px solid transparent;
                                border-bottom: 4px solid transparent;
                            }
                        }
                    }

                }
            }
        }
    }
}

.verification-subtitles {
    .list {
        .item {
            border:$doc-v2-subtypes-select-border;

            &.item-radio input:checked + .radio-content .item-content {
                background: $verification-selected-subtitles-bg;
                color: $card-color;
            }
            .item-content {
                background: $verification-subtitles-bg;
                color: $card-color;
            }
        }
    }
}

#idensic {
    height: 100%;
    overflow-y: auto;
}
