$modalHeader: 236px;
$modalFooter: 97px;

.platform-ios.platform-cordova:not(.fullscreen) {
    .modal-page {
        &.amount-select {
            .scroll-content {
                &.has-header {
                    top: 64px;
                }
            }
        }
        &.countries-select {
            .item-input-favorites {
                top: 64px !important;
            }

            .view-area {
                top: 119px;
            }
        }
        ion-header-bar {
            &.bar-header {
                height: 256px;
                &.bar-stable {
                    height: 64px;
                }
            }
        }
        .scroll-content {
            &.has-header {
                top: 256px;
            }

            &.has-footer {
                bottom: $modalFooter;
            }
        }
    }

}

.modal-page {
    &.amount-select {
        .scroll-content {
            &.has-header {
                top: 44px;
            }
        }
    }

    ion-header-bar {
        background: $main-color;

        &.bar-header {
            height: $modalHeader;

            &.bar-stable {
                height: 44px;
            }
        }

        &.processing {
            background: $model-header-border_processing;
            color: $model-header-border_processing;
        }

        &.failed {
            background: $model-header-border_error;
            color: $model-header-border_error;
        }

        &.success {
            background: $model-header-border_success;
            color: $model-header-border_success;
        }

        .modal-top {
            background: currentColor;
            color: $model-header-text;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;

            &.processing {
                background: $model-header-bg_processing;
            }

            &.failed {
                background: $model-header-bg_error;
            }

            &.success {
                background: $model-header-bg_success;
            }

            .icon {
                font-size: 8em;
                color: currentColor;
            }

            h1 {
                font-size: 1.6em;
                color: currentColor;
                margin-top: 10px;
            }
        }
    }

    .scroll-content {
        .scroll {
            height: 100%;
        }

        .modal-content {
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            padding: 16px;

            .content {
                width: 100%;
                height: 100%;
                align-self: center;
            }

            &.deposit-success {
                justify-content: normal;
                align-items: normal;
                padding: 10px;
                text-align: left;

                .title {
                    color: $placeholder;
                }

                .value {
                    color: $main-color;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }
            }
        }

        &.has-header {
            top: $modalHeader;
        }

        &.has-footer {
            bottom: $modalFooter;
        }
    }

    &.countries-select {
        .item {
            background-color: $modal-select-bg;
            border-color: $border-main;
            color: $content-text;
        }
        .item-input-favorites {
            top: 44px !important;
        }

        .view-area {
            top: 100px;
            & .item {
                border-width: 1px 0;
            }
        }
    }


    &.languages-select{
        .view-area{
            margin-top: 44px;

            .item{
                border-top: none;
                border-left: none;
                border-right: none;
                background-color: $modal-select-bg;
                border-color: $border-main;
                color: $content-text;
                .name-item{
                    display: flex;
                    align-items: center;
                    padding: 0;

                    > .col:not(.pl-1em){
                        padding: 0;
                    }

                    .flag-icon{
                        background-size: contain;
                        padding: 0;
                        outline: 1px solid rgba(0, 0, 0, 0.2);
                        background-position: center;
                        width: 36px;
                        height: 27px;

                        &.flag-icon-ar {
                            background-image: url(../img/flags/sa.svg);
                        }
                    }

                    .ion-ios-checkmark-empty{
                        color: #44C9A3;
                        font-size: 2.5em;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
                }
            }
        }
    }
}

.spinner-processing svg {
    stroke: $spinner-color;
    width: 91px;
    height: 112px;
}

.modal-sms {
    .sms-content {
        color: #fff;
        padding: 16px 16px 0;

        .textHeader {
            line-height: normal;
        }
    }

    .footer {
        height: auto;
    }
}
