.wallet-title {
}

.wallet {
    &.accounts-title {
        padding: 20px 16px;
        font-size: 12px;
        color: $sub-color;
    }
}

.wallet-list {
    margin: 0 16px;
    margin-bottom: 32px;
    box-shadow: 0 2px 2px $document-list-shadow;
    border-color: $document-list-border;
    border-radius: 2px;

    .list {
        padding-bottom: 0;
    }

    ion-item {
        display: flex;
        height: 60px;
        margin: 0;
        padding: 0;
        box-shadow: none;
        border: 1px solid $document-list-border;
        border-bottom: 0;
        border-radius: 0;
        
        .item-content {
            padding: 18px;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: space-between;
            background-color: $document-list-bg;

            .payment-method {
                display: flex;
                height: 22px;
                &.bestpaycard {
                    height: 25px;
                }
            }

            i.icon {
                color: $document-list-arrow;
            }
        }
    }
}