
.flag {
    float: left;
    position: relative;
    margin: 0 5px 0 3px;
    height: 100%;
    width: 42px;
}

.flag1 {
    top: 4px;
    left: 5px;
    z-index: 2;
    background-color: #fff;
    background-size: 18px 13px;
    box-shadow: 1px 1px 2px 0 #000;
}

.flag2 {
    top: 15px;
    left: 17px;
    background-color: #fff;
    background-size: 18px 13px;
    box-shadow: 1px 1px 2px 0 #000;
    z-index: 1;
}

.flag3 {
    top: 5px;
    left: 9px;
    z-index: 1;
}

.flag3.flag-icon {
    width: 30px;
    height: 23px;
}

.flag-icon-image {
    display: block;
    width: 1px;
    height: 24px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 0 50%;
}

.flag-icon {
    /*position: absolute;
    display: inline-block;
    width: 17px;
    height: 13px;
    line-height: normal;*/
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
}

.flag-icon:before {
    content: "\00a0";
}

.flag-icon-background {
    background-repeat: no-repeat;
}
