$height-chart: 55%;
$time-animation: 300ms;

.chart-section {
    display: flex;
    flex-direction: column;

    .chart-toolbar {
        position: absolute;
        z-index: 9999;
        padding: 0.5em;
        margin-left: 0.5em;

        select {
            color: $main-color;
            background: #fff;
            width: auto;
            padding: 0.4em;

            option {
                background: #fff;
                color: $main-color
            }
        }
    }

    &.has-footer {
        bottom: 6.5em;
    }
}

#chart {
    height: 100%;
}

prochart .layout1.htmlchart {
    background-color: $content-bg !important;
}

.chart-graph-wrapper {
    margin-left: -0.4em;
    height: 100%;

    //.dropdown {
    //    .selectItem {
    //        border-color: $graph-dropdown-border;
    //    }
    //}
}

.bottom-navigation {
    height: 10.5em;
    flex-direction: column;
    display: flex;
    justify-content: center;
}

.chart-footer {
    padding: 0;
    height: 6.5em;

    .chart-buttons {
        display: flex;
        align-items: center;
        height: 100%;
        padding: 0 5px;
        border-top: 1px solid $border-main;

        .chart-sell-button {
            background: $state-color-error;
            padding: 0.3em 0.5em;
            border-radius: 2px;
        }

        .chart-buy-button {
            background: $state-color-success;
            padding: 0.3em 0.5em;
            border-radius: 2px;
        }

        .chart-button-wrapper {
            position: relative;
            display: block;
            text-align: left;

            .icon {
                position: absolute;
                right: 0;
                top: 2%;

                &:before {
                    line-height: 1em;
                    font-size: 1.1em;
                }
            }

            .name-button {
                text-align: left;
                font-size: 0.8em;
                display: block;
                line-height: 1em;
                padding-top: 0.2em;
            }

            .value-button {
                display: block;
                line-height: 1em;
                font-size: 1.5em;
                padding-top: 0.2em;
                padding-bottom: 0.25em;
            }

        }
    }
}

.sell-line {
    float: left;
    width: 35%;
    border-bottom: 4px solid $state-color-error;
}

.buy-line {
    float: right;
    width: 64%;
    border-bottom: 4px solid $state-color-success;
}

/* dropdown */
.dropdown {
    vertical-align: top;
    display: inline-block;
    color: $dropdown-text;
    padding: 0;

    li {
        background-color: $dropdown-item-bg_default;
    }

    &.activated {
        .selectItem {
            background: $dropdown-item-bg_selected !important;
        }
    }

    li.selected {
        background-color: $dropdown-item-bg_selected;
    }

    .wrapper-dropdown-list {
        width: 100%;
        border: 1px solid $dropdown-list-border;
        border-top: none;
        background: $dropdown-item-bg;
        top: 0;
        position: relative;
        opacity: 1;
        z-index: 1;
    }

    /*.wrapper-dropdown-list.ng-hide {
