      transition: all $time-animation ease;

    }
  */

    &.range {
        width: 5em;
        font-size: 1.2em;
        margin-right: 0.3em;

        .selectItem {
            border: 1px solid $dropdown-list-border;
            background: $dropdown-item-bg;
            padding: 0.4em;
        }

        .icon.icon-expand_more {
            right: 0;
            top: 15%;
        }

        .wrapper-dropdown-list {
            li {
                span {
                    display: inline-block;
                    width: 100%;
                    padding: 0.6em 0.3em;

                    &.activated {
                        background: $dropdown-item-bg !important;
                    }
                }
            }
        }
    }

    &.type {
        width: 2.5em;
        font-size: 1.5em;

        .selectItem {
            border: 1px solid $dropdown-list-border;
            background: $dropdown-item-bg;
            padding: 0.15em 0.2em;
        }

        .icon.icon-expand_more {
            font-size: 1em;
            right: 0;
            top: 14%;
        }

        .wrapper-dropdown-list {
            li {
                text-align: center;

                .icon-item {
                    padding: 0.5em 0.3em;

                    &.activated {
                        background: $dropdown-item-bg;
                    }
                }
            }
        }
    }

    .selectItem {
        width: 100%;
        position: relative;
        display: inline-block;
        font-size: 1em;
        line-height: 0.8em;
        z-index: 2;

        span {
            width: 100%;
            vertical-align: middle;
            display: inline-block;
        }
    }

    .icon.icon-expand_more {
        position: absolute;
        font-size: 1.2em;
    }
}

.icon-item {
    display: block;
    position: relative !important;
}

.icon.icon-candlestick {
    &:before {
        content: "\e904";
    }
}

.icon.icon-ohlc {
    &:before {
        content: "\e902";
    }
}

.icon.icon-areaspline {
    &:before {
        content: "\e903";
    }
}
