.otp-content {
    .google-get-pandats {
        background: url(../img/google-get.png) no-repeat scroll center center;
        height: 46px;
        width: 153px;
        display: block;
        background-size: contain;
    }

    .apple-get-pandats {
        background: url(../img/apple-get.png) no-repeat scroll center center;
        height: 46px;
        width: 138px;
        display: block;
        background-size: contain;
    }

    .auth-get-pandats {
        background: url(../img/oauth-icon.png) no-repeat scroll center center;
        height: 64px;
        width: 64px;
        display: block;
        background-size: contain;
        background-position: -5px 0;
    }


    .qrcode-pandats {
        .qrcode-wrapper {
            display: flex;
            padding: 10px;
            margin: 10px 0;
            border: 1px solid #cecece;

            img {
                height: 160px;
                object-fit: contain;
            }
        }
    }

    .just-center {
        justify-content: center;
    }

    input.gac-pandats {
        padding: 0 10px !important;
        border: 1px solid #aeaeae;
        margin: 5px 0;
        border-radius: 3px;
    }

    .enable2fa-wrapper {
        margin-top: 10px;

        span {
            font-size: 13px;
            padding: 0 4px;
            display: flex;
            flex: 1;
            margin-right: 3px;
        }
        input[type=checkbox] {
            position: relative;
            width: 18px;
            min-width: 18px;
            height: 18px;
            display: block;
            border: 0;
            background: transparent;
            cursor: pointer;
            -webkit-appearance: none;
            &::before {
                display: table;
                width: 100%;
                height: 100%;
                border-width: 1px;
                border-style: solid;
                border-radius: 18px;
                background: #fff;
                content: ' ';
                transition: background-color 20ms ease-in-out;
                border-color: #ddd;
            }
            &:checked::before {
                border-width: 1px;
                background: #387ef5;
                border-color: #387ef5;
            }
            &::after {
                transition: opacity 0.05s ease-in-out;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
                position: absolute;
                top: 19%;
                left: 21%;
                display: table;
                width: 10px;
                height: 6px;
                border: 1px solid #fff;
                border-top: 0;
                border-right: 0;
                content: ' ';
                opacity: 0;
            }
            &:checked:after {
                opacity: 1;
            }
        }
    }


    form {
        width: 100%;
    }

    .step-wrapper {

        .gac-wrapper {
            position: relative;

            .copyToCB {
                position: absolute;
                right: 0;
                cursor: pointer;
                top: 0;
                bottom: 0;
                width: 28px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                text-align: center;
                font-size: 22px;
            }

        }

        .be-sure {
            font-size: 10.5px;
            line-height: 1;
        }

        .otp-wrapper {
            padding: 0 5px;

            > .layout-column {
                width: 100%;
                padding: 0 10px;

                > .item-input {
                    width: 100%;
                }
            }
        }

    }
}
