.bitgo-content {
    padding: 16px 10px;

    .depAddr-wrapper {
        position: relative;

        .copyToCB {
            position: absolute;
            right: 0;
            cursor: pointer;
            top: 0;
            bottom: 0;
            width: 28px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            font-size: 22px;
        }

    }

    .qrcode-pandats {
        .qrcode-wrapper {
            display: flex;
            padding: 10px;
            margin: 10px 0;
            border: 1px solid #cecece;

            img {
                height: 220px;
                object-fit: contain;
            }
        }
    }

    input.depositAddress {
        padding: 5px 10px !important;
        border: 1px solid #aeaeae;
        margin: 5px 0;
        border-radius: 3px;
        font-size: 13px;
    }

    .layout-column {
        > .layout-row {
            > .layout-col {
                &:last-child {
                    text-align: right;
                }
            }
        }
    }

    .border-bottom {
        border-bottom: 1px solid #e2e2e2;
        padding-bottom: 10px;
        margin-bottom: 10px;
    }
}