.keyboard-container {
    &.modal-page .scroll-content {
        &.has-header {
            top: 44px;
        }

        &.has-footer {
            bottom: 210px;
        }
    }

    .grid-container {
        width: 100%;
        height: 15em;
        display: flex;
        flex-direction: row;

        .grid-col {
            display: flex;
            flex: 1;
            flex-direction: column;

            .button {
                display: flex;
                flex: 1;
                justify-content: center;
                align-items: center;
                font-size: 1.3em;
                border: 1px solid $border-main;
                box-shadow: 0 0 0 rgba(0, 0, 0, 0.3);
                color: $content-text;
                background-color: $main-bg;
                &.activated {
                    background-color: $active-color;
                }
            }
        }
    }

    .invest-buttons {
        padding: 0;
    }

    .button:active {
        background-color: $active-color;
        color: #fff;
    }

    .view-input {
        border-radius: 2px;
        background: $input-invest-bg;
        color: $input-invest-text;
        border: 1px solid transparent;
        width: 100%;
        margin-left: -1px;
        min-height: 3.5rem;
        padding: 15px;
        display: block;
        justify-content: center;
        align-items: center;
        font-size: 1.5rem;
        margin-top: 1em;
    }

}
