.platform-ios.platform-cordova:not(.fullscreen) {
    & .popover-backdrop {
        .popover-wrapper {
            .popover {
                top: 20px !important;
            }
        }
    }
}

.popover-backdrop {
    &.active {
        background-color: transparent;
    }
    .popover-wrapper {
        .popover {
            top: 0 !important;
            margin-top: 6px;
            border-radius: 0;

            &.instruments-options {
                height: 96px;
            }
            &.manageaccount-options, &.withdraw-options {
                height: auto;
                .scroll-content {
                    height: min-content;
                }
            }

            &.wallet-options {
                height: 48px
            }

            .scroll-content {
                margin: 0;
                border-radius: 0;

                .options-menu {
                    .button {
                        text-align: left;
                        margin: 0;
                        border-radius: 0;
                        background-color: $option-button-bg;
                        color: $option-button-text;

                        &.activated {
                            border: none;
                            background-color: $option-button-bg_active;
                        }
                    }
                }
            }
        }
        .popover-arrow {
            display: none;
        }
    }
}

