.login {
    input[type="text"] {
        -webkit-user-select: text;
    }

    .checkbox {
        padding-left: 0;
    }
}

.add-line {
    position: relative;

    &::before, &::after {
        content: '';
        display: inline-block;
        position: absolute;
        width: 27%;
        height: 1px;
        top: 50%;
    }

    &:before {
        border-top: 1px solid $border-main;
        left: 4%;
    }
    &:after {
        border-top: 1px solid $border-main;
        right: 4%;
    }
}

.show-password {
    .checkbox {
        input:before {
            border-radius: 0;
        }
    }
}

.show-password {
    .show-password {
        .checkbox-icon:before {
            border-radius: 0;
        }
    }
}

.item-checkbox.item-checkbox {
    padding-left: 45px;
}

.show-password.item-checkbox {
    .checkbox {
        left: 0 !important;
    }
}


