/* colors */
.white {
    color: #fff !important;
}

.black {
    color: #000;
}

/* fonts */

.open-sans {
    font-family: "Open Sans", sans-serif;
}

.footer {
    a {
        text-decoration: none;
        color: $footer-text;
    }

    .main-menu {
        i {
            color: $footer-text;
        }
    }
}

.up {
    color: $state-color-success;
}

.down {
    color: $state-color-error;
}

.muted {
    color: $placeholder;
}

logo-title {
    text-transform: uppercase;

    & img {
        vertical-align: middle;
        width: $logo-width;
    }
}

.spinner-text {
    color: $spinner-color;
    margin: 0.3em;
}

.overflow-scroll {
    &.keyboard-up:not(.keyboard-up-confirm) {
        -webkit-overflow-scrolling: auto;
    }
}

.vertical-align {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vw;

    div.vertical-align-item {
        max-width: 85%;
        &.fullwidth {
            max-width: 100%;
        }
        div {
            line-height: 1.2em;
        }

        div:last-of-type {
            text-align: center;
        }
    }
}

.bg-l-grey {
    background-color: $content-bg;
}

.helper-text {
    color: $helper-text;
}

.blue-text {
    color: $active-color;
}

.red-text {
    color: $state-color-error;
}

a {
    color: $active-color;
}

// Pages content bg
.pane, .view {
    background-color: $content-bg;
    color: $content-text;
}

ion-content {
    background-color: $content-bg;
    color: $content-text;

    &.pending-content{
        background-color: $pending-content-bg;
    }

    &.order-content {
        background-color: $order-content-bg;
    }
}

.layout-row {
    display: flex;
    flex-direction: row;

    &.shrinkable {
        flex: 1;
        overflow-y: hidden;
    }

    &.just-space {
        justify-content: space-between;
    }
}

.layout-column {
    display: flex;
    flex-direction: column;

    &.shrinkable {
        flex: 1;
        min-width: 0;
    }
}

.full-width {
    width: 100%;
}

.full-height {
    height: 100%;
}

.hidden-content {
    > * {
        visibility: hidden;
    }
}

.just-center {
    justify-content: center;
}

.font-bold {
    font-weight: bold;
}

.full-width {
    width: 100%;
}

.semi-width {
    width: 50%;
}

.full-height {
    height: 100%;

    &.has-header,
    &.has-footer {
        height: calc(100% - 44px);
    }

    &.has-footer.has-header {
        height: calc(100% - 88px);
    }
}
