.credit-card,
.amount-select {
    input[type="date"] {
        padding-top: 0;
    }

    .radio-toolbar {
        label {
            margin: 0;
            span {
                color: $content-text;
            }
        }
    }

    .expiryDate {
        background: transparent;
    }

    .incdec-amount {
        height: 12em;

        .amount-area {
            color: $credit-amount-text;

            .button {
                color: currentColor;
                background-color: transparent;
            }

            .amount-input-currency {
                color: currentColor;
            }

            .wrapper-amount {
                background-color: $credit-amount-bg;
                border-bottom: 1px solid $credit-amount-border;

                > input {
                    color: currentColor;
                    background-color: $credit-amount-bg_price;
                }
            }
        }
    }

}
