.bar.subheader-color {
    background: $subheader-bg;

    .menuWrap {
        white-space: nowrap;
        overflow-x: scroll;
        color: $header-tabs-text;

        &::-webkit-scrollbar {
            display: none;
        }

        .titleHolder {
            position: relative;
            display: inline-block;
            padding: .613em .5em;

            .titleItem {
                display: block;
                border-radius: 3px;
                padding: 0 .7em;

                &.active-category,
                &.active-category.activated {
                    color: $header-tabs-text_active;
                    background-color: $header-tabs-bg_active;
                }
            }
        }

    }
}
