#asset-info {
    background-color: $content-bg;
    .asset-info {
        margin-top: 1em;
        .item {
            border: 0;
            font-size: 12px;
            padding-top: 0.1em;
            padding-bottom: 0.1em;
            background-color: $content-bg;
            .info {
                border-bottom: 1px solid $border-main;
                padding-top: 0.6em;
                padding-bottom: 0.6em;
                //align-items: center;
                &.no-border {
                    border: 0;
                }
                &.no-padding {
                    padding: 0;
                }
                .info-label {
                    span:first-of-type {
                        position: relative;
                        top: 2px;
                        left: -4px;
                        font-size: 1.5em;
                    }
                }
                .info-value {
                    text-align: right;
                    color: $sub-color;

                    .time-range {
                        margin-bottom: 0.5em;
                    }
                }
            }
        }
    }
}

