.main-img {
    background: url("../img/welcome.jpg") 100% 100% no-repeat;
    background-size: cover;

    & .logo {
        position: absolute;
        width: 75%;
        left: 50%;
        top: $logo-top;
        transform: translateX(-50%);
        text-align: center;
    }
}

.welcome-page-content {
    .localization-wrapper {
        position: relative;
        height: 37px;
        flex: 1;
        .localization-area {
            color: #ffffff;
            justify-content: center;
            align-items: center;
            display: flex;
            padding: 0;
            position: relative;
        }
    }

    .navigation-content {
        .languageSelector {
            align-items: center;
            justify-content: space-between;
        }
        .content {
            margin: 10px 0;
        }
        .welcome-message {
            min-height: 37px;
            line-height: 37px;

            .login {
                color: $loginButton-color;
            }
        }

        .col {
            margin: 0;
            padding: 0;
        }
    }
}
