.pending-item {
    color: $trade-text;

    .output-name {
        color: $trade-text;
    }

    .muted {
        color: $trade-status-name;
    }
}

.pending-content {
    background: $pending-content-bg;
    padding-top: 0.32em;
}

.pending-items {
    border: none;
    background: $pending-content-bg;
    padding: 0.45em 0.7em 0.45em 0.7em;
}

.border-right-pending {
    border-right: 1px solid $trade-border;
}

.pending-section {
    div {
        margin: 0.4em 0.5em;
    }
}
