.languageSelect{
    display: flex;
    padding: 0;
    height: 100%;
    width: 100%;

    .flag-union {
        background-size: 24px;
        display: inline-block;
        background-position: center;
        background-repeat: no-repeat;
        margin: 0 6px;

        width: 24px;
        height: 24px;
    }

    .result{
        justify-content: flex-end;
        align-items: center;
        padding: 5px 0;


        span{
            line-height: 27px;
            display: flex;
            margin: 0 6px;
        }

        i.icon{
            position: relative;
            padding-left: 45px;
            background-position: left;

            &.flag-icon-ar {
                background-image: url(../img/flags/sa.svg);
            }

            &::after{
                content: '';
                width: 10px;
                height: 5px;
                position: absolute;
                border-bottom: none;
                border-left: 5px solid transparent;
                border-right: 5px solid transparent;
                border-top: 6px solid #ffffff;
                top: calc(50% - 3px);
                right: 0;
            }
        }


    }

    .optionsWrapper {
        position: fixed;
        height: calc(100% - 15px);
        width: auto;
        overflow-y: auto;
        box-sizing: border-box;
        z-index: 100;
        //overflow-scrolling: touch;

        .optionsList {
            background: #000000;
            border: 1px solid #aaaaaa;

            .option{
                padding: 6px 0;
                color: #ffffff;
                margin: 0;
                background: none;
                border: 0;

                &.activated{
                    background: rgba(255, 255, 255, .2);
                }

                span{
                    line-height: 24px;
                    white-space: nowrap;
                    padding: 0 5px;
                }
            }

        }

    }
}