#closedTrades {
    & .closedtrade-item {
        position: relative;
        color: $trade-text;

        .trade-name {
            color: currentColor;
        }

        .border-right-order {
            border-color: $trade-border;
        }

        .status {
            color: $trade-status-name;
        }
        .profitValueUSD {
            opacity: 0.7;
            display: block;
            margin-top: 2px;
        }
        .current-state {
            .title {
                display: inline-block;
                min-width: 3em;
                padding: 0.25em .2em .2em;
                margin-right: 0.3em;
                text-align: center;
                text-transform: capitalize;
            }

            &.sell,
            &.sell_limit,
            &.sell_stop {
                & .title {
                    background: $cards-state-bg_sell;
                    color: $cards-state-text;
                }
                & .value {
                    color: $cards-state-bg_sell;
                }
            }

            &.buy,
            &.buy_limit,
            &.buy_stop {
                & .title {
                    background: $cards-state-bg_buy;
                    color: $cards-state-text;
                }
                & .value {
                    color: $cards-state-bg_buy;
                }
            }
        }

        .openDate,
        .closeDate {
            line-height: 1.4em;
            margin-top: 5px;
            @if variable-exists(trade-date-color) {
                color: $trade-date-color;
            }
        }
    }

    & .order-content {
        & .order-items {
            margin-bottom: .5em;
        }
    }
}
