.transaction-history {

    ion-content {
        bottom: 8px !important;
    }

    .title-line {
        .col {
            text-transform: uppercase;
            //line-height: 34px;
            color: $placeholder;
        }
    }

    .transactions-list {
        margin: 0 16px;

        .item {
            border: none;
            background-color: $content-bg;
            
            .card {
                margin: 10px 2px;
                background-color: $account-cards-bg;
                color: $account-cards-status-name;

                .row {
                    justify-content: space-between;
                    padding: 10px;

                    .button {
                        min-height: 38px;
                        margin: 0;
                        height: 38px;
                        line-height: normal;
                    }

                    .value {
                        color: $content-text;
                        font-weight: bold;
                    }

                    &.top {
                        border-bottom: 1px solid $border-main;
                    }

                    &.bottom {
                        margin: 0;
                        line-height: normal;
                    }
                }
            }
        }

    }

}
