.invite-friends-page {

    padding-right: 30px;

    .sub-head-pandats {
        padding: 20px 0 0;
        min-height: 33px;
    }

    .horizontal-break-pandats {
        border-bottom: 1px solid $amount-input-border;
        margin: 5px 0 20px;
    }

    .sns-area {
        margin-top: 10px;

        .sns-button-pandats {
            padding: 10px 12px;
            width: 110px;
            border: 1px solid $amount-input-border;
            background: $border-main;
            color: $content-text;
            border-radius: 30px;
            cursor: pointer;

            + .sns-button-pandats {
                margin-left: 5px;
            }
        }
    }

    input[name = 'link'] {
        background: $placeholder;
        border-radius: 0;
        border: 1px solid $amount-input-border;
        color: $content-text;
        padding: 0 12px !important;
        box-sizing: border-box;
        height: 100%;
    }

    .link-input-wrapper {
        .layout-column {
            + .layout-column {
                margin-left: 8px;
            }

            button {
                min-height: 10px;
                height: auto;
                padding: 9px 7px;
                line-height: 1;
                border-radius: 0;

                &:hover {
                    color: $header-text;
                }
            }
        }
    }

    .via-pandats {
        padding: 14px 0;
    }

    .item-complex {
        .item-content {
            padding: 20px;
        }
    }

    > .layout-column {
        > .layout-row {
            > .layout-column {
                &.link-area {
                    .forex-button-pandats {
                        font-size: 12px;
                        height: 35px;
                        padding: 8px 15px;
                    }
                }

                .earnings-area {
                    > .layout-row {
                        &:first-child {
                            margin-top: 12px;
                        }
                    }
                }

                > .layout-row {
                }
            }
        }
    }



    .blank-area {
        //border: 1px dashed themeColor('invite-friend', 'borderBlankArea');
        padding: 20px;
        text-align: center;
    }

    .add-account-button-pandats {
        padding: 10px 20px;
        width: auto;
        height: auto;
    }

    .flag-icon-pandats {
        position: inherit;
        box-shadow: none;
        height: 17px;
        width: 17px;
        background-color: transparent;
    }

    .child-pages-list {
        .list {
            padding-bottom: 0;
        }

        .card.item {
            background-color: $document-list-bg;
        }

        ion-item {
            height: 60px;
            padding: 0;
            box-shadow: none;
            border: 1px solid $document-list-border;
            border-bottom: 0;
            border-radius: 0;

            &:last-of-type {
            }

            .item-content {
                padding: 16px;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                color: $document-list-text;
                background-color: $document-list-bg;

                > span {
                    color: currentColor;
                }

                > i.icon {
                    color: $document-list-arrow;
                }

                .title {
                    display: flex;
                    align-items: center;
                    color: currentColor;

                    > span {
                        color: currentColor;
                    }

                    i.icon {
                        font-size: 24px;
                        margin-right: 12px;
                        color: $document-list-icon;

                        &.icon-proof_of_identity:before {
                            content: "\e92a";
                        }

                        &.icon-proof_of_residence:before {
                            content: "\e92d";
                        }

                        &.icon-proof_of_payment:before {
                            content: "\e928";
                        }
                        &.icon-document_other:before {
                            content: "\e92d";
                        }
                    }
                }
            }

            .file-title {
                color: $document-file-title;
            }

            .file-subtitle {
                color: $document-file-subtitle;
            }
        }
    }
}
