.account-transfer {
    .incdec-amount {
        .amount-area {
            .wrapper-amount {
                background-color: $transfer-amount-input-bg;
                border-bottom: 1px solid $transfer-amount-input-border;

                font-size: 8px;
                width: 64%;
            }

            & .amount-input-currency {
                color: $transfer-amount-color;
            }

            input[type="number"] {
                background-color: transparent;
                color: $transfer-amount-color;
            }
        }

        button.button-down,
        button.button-up,
        input[type="text"],
        input[type="number"] {
            background: $content-bg;
            color: $transfer-amount-color;

            &.disable-user-behavior.activated {
                background: $transfer-amount-input-disable;
            }
        }
    }
}