.invest-page {
    .has-footer {
        bottom: 88px;
    }
}

.invest-top {
    background: $header-bg;
    color: $header-text;

    .rate {
        text-align: center;

        .sell {
            color: $state-color-error;
        }
    }

    .bordered {
        position: relative;

        &:after {
            content: '';
            position: absolute;
            top: 15%;
            right: 0;
            height: 70%;
            border-right: 1px solid $border-main;
        }
    }

    .convertusd {
        margin-top: -5px;
        color: $state-color-success;
    }
}

.currency-select-area {
    background: $header-bg;
    color: $header-text;
    padding: 0 5px;

    .form-group {
        width: 100%;
    }
}

.invest-profitloss {
    .item-checkbox {
        font-size: 12px;
    }

    .padding-none {
        > .row {
            padding: 0;

            > .col {
                padding: 0;
            }
        }
    }

    label.item-checkbox {
        min-height: 2.5em;
        padding-bottom: 0;
        padding-right: 0;
        display: flex;
        align-items: center;
    }

    .item-checkbox {
        border: 0;
        padding-top: 0;

        .checkbox {
            top: 50%;
            left: 0;
            padding-left: 0;
        }

        .item-content {
            white-space: normal;
            line-height: 0.8;
            overflow: visible
        }
    }

    .current {
        margin-top: 1em;
    }

    .item-checkbox.item-checkbox {
        padding-left: 2.5em;
    }

    .invest-input {
        border-radius: 2px;
        background: $input-invest-bg;
        color: $input-invest-text;
        border: 1px solid transparent;
        text-indent: 1em;
        width: 100%;
        margin-left: -1px;
        min-height: 2.2rem;
        padding: 5px;

        &.locked {
            border-color: $input-invest-border_locked;
        }

        &:focus {
            background: $input-invest-bg_focus;
            color: $input-invest-text_focus;
            border-color: $placeholder;
        }
    }

    .equal-height {
        height: 3.5em;
        white-space: nowrap;
        align-items: center;
        padding-top: 0;
    }

    .invest-money {
        border-radius: 2px;
        background: $input-invest-bg_inactive;
        color: $input-invest-text_inactive;
        text-indent: 1em;
        width: 100%;
        height: 2.5em;
        line-height: 2.5em;
    }

    .reach {
        height: auto;
        margin-top: inherit;
        padding-top: 5px;

        .reachWrap {
            border-bottom: 1px solid $border-main;
        }
    }
    .sltpWrap {
        border-bottom: 1px solid $border-main;
    }
}

.invest-value-color {
    color: $state-color-success;
}

.invest-amount {
    text-align: center;

    .wrapper-amount {
        position: relative;
        display: inline-block;
        width: 50%;
        height: 5em;

        span {
            position: absolute;
            width: 100%;
            top: 5%;
            color: $invest-current-rate-text;
        }
    }

    .amount-area {
        .messages {
            display: flex;
            justify-content: center;
            height: 20px;
            font-size: 12px;
            color: $state-color-error;
        }
    }

    button.button-down, button.button-up, input[type="text"], input[type="number"] {
        border: 0;
        background: transparent;
        color: $btn-rate-color;
        font-size: 2em;
    }

    input[type="text"].amount, input[type="number"].amount {
        border: 0;
        border-radius: 2px;
        height: 90%;
        width: 100%;
        //padding-left: 3em;
        font-size: 2.125em;
        padding-top: 0.5em !important;
        text-align: center;
        background-color: $invest-current-rate-bg;
        color: $invest-current-rate-text;
    }

}

.inc-dec {
    $border-width: 1px;

    button, input[type="text"], input[type="number"] {
        display: inline-block;
        margin: 0;
        padding: 0;
        min-width: 0;
        min-height: 0;
        border-radius: 0;
        width: 20%;
        height: 2.3em;
        outline: 0;
        box-sizing: border-box;
        line-height: 1;
        background: $input-bg;
        color: $main-color;
    }

    input[type="text"] {
        text-align: center;
        padding-left: .1em;
        padding-right: .1em;
        width: 50%;
        height: 2em;
        font-size: 1em;
        border: $border-width solid $main-color;
        background: $input-bg;
    }

    button.button-left {
        margin-right: -4px;
        border: {
            style: solid;
            color: $main-color;
            left: {
                width: $border-width;
            }
            right: {
                width: 0;
            }
            top: {
                width: $border-width;
            }
            bottom: {
                width: $border-width;
            }
        }
    }

    button.button-right {
        margin-left: -4px;
        border: {
            style: solid;
            color: $main-color;
            left: {
                width: 0;
            }
            right: {
                width: $border-width;
            }
            top: {
                width: $border-width;
            }
            bottom: {
                width: $border-width;
            }
        }
    }

    .disable-user-behavior.activated {
        background: $placeholder;
    }
}

.invest-inc-dec {
    button, input[type="text"], input[type="number"] {
        height: 2em;
    }
}

