.current {
    color: $state-color-success;
}

.margin-value {
    color: $placeholder;
}

.border-right {
    border-right: 1px solid $border-main;
}

.border-left {
    border-left: 1px solid $border-main;
}

.invest-buttons {
    background-size: 0;
    height: auto;

    button {
        padding: 0 12px !important;
        min-width: 52px !important;
        min-height: 47px !important;
        border-width: 2px;
        border-radius: 2px;
        vertical-align: top;
        text-align: center;
        text-overflow: ellipsis;
        font-size: 16px !important;
        line-height: 42px !important;
    }

    .invest-buy-button {
        &.sell,
        &.sell_limit,
        &.sell_stop {
            background: $state-color-error;

            &.activated {
                background: lighten($state-color-error, 10%);
            }
        }
    }

    .invest-cancel-button {
        &.buy,
        &.buy_limit,
        &.buy_stop {
            color: $state-color-success;
            border-color: $state-color-success;
        }

        &.sell,
        &.sell_limit,
        &.sell_stop {
            color: $state-color-error;
            border-color: $state-color-error;
        }
    }
}

.modal-footer {
    background-size: 0;
    height: auto;

    button {
        padding: 0 12px !important;
        min-width: 52px !important;
        min-height: 47px !important;
        border-width: 2px;
        border-radius: 2px;
        vertical-align: top;
        text-align: center;
        text-overflow: ellipsis;
        font-size: 16px !important;
        line-height: 42px !important;
    }
}

.divTxt {
    border: 0;
    border-radius: 2px;
    height: 90%;
    width: 100%;
    font-size: 2.125em;
    text-align: center;
    background-color: $input-invest-bg;
    color: $input-invest-text;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: .5em !important;
}




