$time-animation: 200ms;

.footer-menu {
    .tab-nav {
        background: $footer-bg;
        height: 54px;
        border-bottom: none;
        padding: 0;

        a {
            //@include animateMenu($time-animation);

            position: relative;
            color: $footer-tabs-text_default;
            flex: inherit;
            max-width: inherit;
            width: 18%;
            height: 100%;
            font-size: 1.5em;
            border-width: 0 !important;
            opacity: 1;

            &.verificationRequered:not(.tab-item-active):before {
                position: absolute;
                content: ' ';
                display: flex;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: $state-color-error;
                top: 5px;
                right: 5px;
            }

            &.activated {
                border-width: 0 !important;
                margin-top: 0;
                color: $footer-tabs-text_click !important;
                background-color: $footer-tabs-bg_click;
            }

            i {
                //@include animateMenu($time-animation);

                position: relative;
                font-size: 2.4em;
                height: 100%;
                width: 100%;

                &:before {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }

            span {
                //@include animateMenu($time-animation);

                position: absolute;
                display: block;
                width: 100%;
                opacity: 0;
                font-family: "Roboto Light", sans-serif;
                font-size: 1.2em;
                bottom: -2.5em;
            }

            &.tab-item-active {
                width: 28%;
                height: 120%;
                opacity: 1;
                margin-top: -1em;
                overflow: inherit !important;
                background: $footer-tabs-bg_active;
                color: $footer-tabs-text_active !important;

                i {
                    width: 100%;
                    height: 100%;
                    color: currentColor;
                    font-size: 2.1em;
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;

                    &:before {
                        align-items: baseline;
                        padding-top: .7em;
                    }
                }

                span {
                    width: 100%;
                    color: currentColor;
                    opacity: 1;
                    bottom: .7em;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }

        }
    }
}

