.withdrawal-compliance {

    // https://github.com/ionic-team/ionic-v1/issues/137
    ion-content {
        height: auto !important;


        form {
            padding-bottom: 50px;

            .checklist {
                > label {
                    display: inline-block;
                }
            }

            label.checkbox-wrapper {
                direction: rtl;
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                font-size: 14px;
                line-height: 1.42857;
                margin-top: 10px;

                > input {
                    margin-right: 5px;
                }
            }

            input {
                &[type=text] {
                    display: none;
                    border-bottom: 1px solid #e2e2e2;

                    &.shown {
                        display: initial;
                    }
                }
            }

            .separator-title {
                margin: 1.6em 0 0 0;
                font-size: 1.3em;
                text-align: center;
                font-weight: bold;
            }

            .question-title {
                padding: 15px 0;
                margin-top: 20px;
                border-top: 1px solid #9C9C9C;
                color: #929292;
                &.first {
                    border: none;
                    margin-top: 0;
                }
            }


            .tap-checklist ,
            .checkbox-wrapper {
                input[type=checkbox] {
                    position: relative;
                    width: 18px;
                    min-width: 18px;
                    height: 18px;
                    display: block;
                    border: 0;
                    background: transparent;
                    cursor: pointer;
                    -webkit-appearance: none;
                    &::before {
                        display: table;
                        width: 100%;
                        height: 100%;
                        border-width: 1px;
                        border-style: solid;
                        border-radius: 18px;
                        background: #fff;
                        content: ' ';
                        transition: background-color 20ms ease-in-out;
                        border-color: #ddd;
                    }
                    &:checked::before {
                        border-width: 1px;
                        background: #387ef5;
                        border-color: #387ef5;
                    }
                    &::after {
                        transition: opacity 0.05s ease-in-out;
                        -webkit-transform: rotate(-45deg);
                        transform: rotate(-45deg);
                        position: absolute;
                        top: 19%;
                        left: 21%;
                        display: table;
                        width: 10px;
                        height: 6px;
                        border: 1px solid #fff;
                        border-top: 0;
                        border-right: 0;
                        content: ' ';
                        opacity: 0;
                    }
                    &:checked:after {
                        opacity: 1;
                    }
                }
                label {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                }
                span {
                    font-size: 13px;
                    padding: 0 4px;
                    display: flex;
                    flex: 1;
                    margin-right: 3px;
                }

                > div {
                    > label {
                        display: inline-flex;
                        flex-wrap: wrap;
                        //flex: 0 0 49%;
                        margin: 0 0 10px;
                        width: 50%;
                    }
                }
            }


        }
    }


}