.platform-ios.platform-cordova:not(.fullscreen) {
    .manage-accounts .active-account-subheader {
        top: 64px !important;
    }

    .manage-accounts .accounts-title {
        top: 64px !important;

        &.hasActive {
            top: 188px !important;
        }
    }

    .manage-accounts .accounts-content {
        top: 108px !important;

        &.hasActive {
            top: 232px !important;
        }
    }
}

.manage-accounts {
    & .active-account-subheader {
        background: $header-bg;
        flex-direction: column;
        top: 44px !important;

        & .card {
            .accountType {
                .demo {
                    background-color: $account-cards-label-demo;
                }

                .live {
                    background-color: $account-cards-label-real;
                }
            }
        }
    }

    & .accounts-title {
        top: 44px !important;
        background-color: $account-content-bg;
        color: $account-cards-list-title;

        &.hasActive {
            top: 168px !important;
        }
    }

    & .card {
        font-size: 14px;
        margin: 0 10px 10px;
        background-color: $account-cards-bg;

        h3.name {
            color: $account-cards-title;
            font-weight: bold;
        }

        .item-text-wrap {
            padding: 10px 10px 5px;
            background-color: $account-cards-bg;

            .accountType {
                position: absolute;
                overflow: hidden;
                top: 0;
                right: 0;
                width: 60px;
                height: 60px;

                & span {
                    position: absolute;
                    color: $cards-state-text;
                    font-size: 11px;
                    text-transform: uppercase;
                    letter-spacing: 1px;
                    top: 10px;
                    right: -20px;
                    width: 80px;
                    text-align: center;
                    transform: rotate(44deg);
                }
            }
        }

        .content,
        .infoTitle,
        .infoValues {
            font-size: 14px;
        }

        .content {
            color: $account-cards-subtitle;
        }

        .infoValues {
            font-weight: bold;
            font-size: 10px;
            color: $account-cards-status-value;
            line-height: 10px;
        }

        .infoTitle {
            font-size: 11px;
            text-transform: uppercase;
            color: $account-cards-status-name;
        }

        .info {
            margin-top: 7px;
            padding-top: 5px;
            border-top: $account-cards-border 1px solid;
        }
    }

    & .accounts-content {
        top: 88px !important;
        padding-top: 10px;
        background-color: $account-content-bg;

        .realAccounts {
            .card:first-child {
                margin-top: 0 !important;
            }

            .accountType span {
                background-color: $account-cards-label-real;
            }
        }

        .demoAccounts {
            .accountType span {
                background-color: $account-cards-label-demo;
            }
        }

        &.hasActive {
            top: 212px !important;
        }
    }
}

.accounts-subheader {
    background: #fff;
    color: $placeholder;
}

