.modify-trade {
    color: $modify-text;
    .has-footer {
        bottom: 88px;
    }
    .trade-info {
        background: $modify-info-bg;

        .sub-title {
            padding-top: .7em;
            padding-bottom: 1em;
        }

        .title {
            h3 {
                color: currentColor;
            }
        }

        .labels {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;

            .label {
                //height: 2.5em;
                display: inline-block;
                padding: 0.5em 0.55em;
                background: $modify-label-bg;
                text-align: center;
            }

            &.short .label {
                color: $state-color-error;
                border: 1px solid $state-color-error;
                border-radius: 0 3px 3px 0;

                &.action {
                    color: #fff;
                    background: $state-color-error;
                    border-radius: 3px 0 0 3px;
                }
            }
            &.buy .label {
                color: $state-color-success;
                border: 1px solid $state-color-success;
                border-radius: 0 3px 3px 0;

                &.action {
                    color: #fff;
                    background: $state-color-success;
                    border-radius: 3px 0 0 3px;
                }
            }

        }

        .price {
            color: $sub-color;
            margin-bottom: .7em;
            white-space: nowrap;
            .col {
                div {
                    padding: 0.3em;
                }
            }
        }

        .border-top {
            border-top: 1px solid $border-main;
        }

        .profit {
            .help-wrapper {
                color: $modify-profit-text;
                position: relative;
                background: $modify-profit-bg;
                display: inline-flex;
                justify-content: flex-end;

                &:before {
                    content: "";
                    position: absolute;
                    right: 100%;
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0 1.5em 2em 0;
                    border-color: transparent $modify-profit-bg transparent transparent;
                }

                span {
                    padding: 0.7em 0.7em;
                }
            }

        }
    }

    .invest-input {
        > div {
            line-height: 1.7;
        }
    }

    .invest-profitloss {
        background: $modify-invest-bg;
    }
    .closeTradeOptions {
        position: absolute;
        z-index: 999;
        //top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $modify-invest-bg;
        display: flex;
        flex-direction: column;
        box-shadow: 0px 4px 7px 3px #111;
        @include animateOnce(moveUp, 0.2s, ease-out);

        &.ng-hide {
            @include animateOnce(moveDown, 0.2s, ease-out);
        }

        .closeTradeTitle {
            background-color: $modify-partial-title-bg;
            padding: 10px 0 10px 0.6em;
            font-size: 1.2em;
        }

        .partialOption {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            margin: 0;
            padding-top: 2em;
            .item-checkbox.item-checkbox {
                border: 0;
                min-height: 2.5em;
                padding-top: 0;
                padding-bottom: 0;
                padding-right: 0;
                display: flex;
                align-items: center;
            }
            .invest-amount {
                .amount-area {
                    display: flex;
                    .wrapper-amount {
                        display: flex;
                        flex: 1;
                        flex-direction: column;
                        align-items: center;
                        background-color: $input-invest-bg;
                        height: auto;
                        padding: 3px 0;
                        .amountTitle {
                            position: relative;
                        }
                        .divTxt {
                            padding: 0 !important;
                            background-color: transparent;
                            font-size: 2em;
                            height: auto;
                        }
                    }
                    button.button-down,
                    button.button-up {
                        color: $input-invest-text;
                        &:after {
                            top: 0;
                            bottom: 0;
                            left: 0;
                            right: 0;
                        }
                    }
                }
            }
        }

        .actions {
            display: flex;
        }
    }
    .modern-checkbox {
        .checkbox-icon:after {
            top: 22%;
        }
    }
}

@keyframes moveUp {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes moveDown {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(100%);
    }
}

