/*
To customize the look and feel of Ionic, you can override the variables
in ionic's _variables.scss file.

For example, you might change some of the default colors:

$light:                           #fff !default;
$stable:                          #f8f8f8 !default;
$positive:                        #387ef5 !default;
$calm:                            #11c1f3 !default;
$balanced:                        #33cd5f !default;
$energized:                       #ffc900 !default;
$assertive:                       #ef473a !default;
$royal:                           #886aea !default;
$dark:                            #444 !default;
*/

// The path for our ionicons font files, relative to the built CSS in www/css
$ionicons-font-path: "../lib/ionic/fonts" !default;

// Include all of Ionic
@import "www/lib/ionic/scss/ionic",
"custom-styles/import.scss";

.invest-amount {
    input {
        border: 1px solid $input-border_default;
    }
}

.app-footer {
    height: 3.125em;
}

.width-100 {
    width: 100%;
}

.width-95 {
    width: 95%;
}

.width-80 {
    width: 80%;
}

.width-70 {
    width: 70%;
}

.height-80 {
    height: 80%;
}

.height-100 {
    height: 100%;
}

.height-auto {
    height: auto;
}

.position-bottom {
    position: absolute;
    bottom: 0;
}

.bottom-border {
    border-bottom: 1px solid $border-main;
}

.center-text {
    text-align: center;
}

.text-right {
    text-align: right;
}

.mt-5 {
    margin-top: 0.313em;
}

.mt-10 {
    margin-top: 0.625em;
}

.mr-3 {
    margin-right: 0.015em;
}

.mb-3 {
    margin-bottom: 0.25em;
}

.mb-5 {
    margin-bottom: 0.313em;
}

.mb-10 {
    margin-bottom: 0.625em;
}

.mg-0 {
    margin: 0;
}

.p-0 {
    padding: 0;
}

.p-1 {
    padding: 1em;
}

.p-2 {
    padding: 2em;
}

.p-3 {
    padding: 3em;
}

.pl-0 {
    padding-left: 0;
}

.pl-10 {
    padding-left: 0.625em;
}

.pt-0 {
    padding-top: 0;
}

.pt-2 {
    padding-top: 0.15em;
}

.pt-11 {
    padding-top: 0.688em;
}

.pl-5 {
    padding-left: 0.2em;
}

.pl-7 {
    padding-left: 0.35em;
}

.pl-15 {
    padding-left: 0.938em;
}

.pb-0 {
    padding-bottom: 0;
}

.pb-5 {
    padding-bottom: 0.2em;
}

.pb-7 {
    padding-bottom: 0.35em;
}

.pb-10 {
    padding-bottom: 0.625em;
}

.pr-0 {
    padding-right: 0;
}

.pr-5 {
    padding-right: 0.2em;
}

.pr-7 {
    padding-right: 0.35em;
}

.pr-10 {
    padding-right: 0.625em;
}

.font-10 {
    font-size: 0.625em;
}

.font-11 {
    font-size: 0.688em;
}

.font-12 {
    font-size: 0.750em;
}

.font-13 {
    font-size: 0.813em;
}

.font-14 {
    font-size: 0.875em;
}

.font-15 {
    font-size: 0.938em;
}

.font-18 {
    font-size: 1.125em;
}

.font-22 {
    font-size: 1.375em;
}

.font-40 {
    font-size: 2.5em;
}

.line-height-2 {
    line-height: 2em;
}

.lh-1 {
    line-height: 1.5em;
}

.text-transform-uppercase {
    text-transform: uppercase;
}

.border-0 {
    border: none;
}

.inline-block {
    display: inline-block;
}

.display-block {
    display: block;
}

.float-left {
    float: left;
}

.float-right {
    float: right;
}

.flex-1 {
    flex: 1;
}

.underline-none {
    text-decoration: none;
}

.display-flex {
    display: flex;
}

.align-self {
    align-self: center;
}

/* colors */
.white {
    color: #fff !important;
}

.black {
    color: #000;
}

/* fonts */
.open-sans {
    font-family: "Open Sans", sans-serif;
}

/* footer */
.footer-bg {
    background: $footer-bg;
}

.footer {
    a {
        text-decoration: none;
        color: $footer-text;
    }

    .main-menu {
        i {
            color: $footer-text;
        }
    }
}

.bar-footer {
    background-color: $content-bg;
    color: $content-text;
}

//titlebar
.bar.bar-stable {
    background-image: none;
    border: 0;
    border-color: transparent;
    background: $header-bg;
    color: $header-text;

    .title {
        color: $header-text;
    }
}

