@mixin animateOnce($keyframe, $duration, $transition: linear) {
  animation: $keyframe $duration $transition;
  animation-iteration-count: 1;

  -webkit-animation: $keyframe $duration $transition;
  -webkit-animation-iteration-count: 1;

  -moz-animation: $keyframe $duration $transition;
  -moz-animation-iteration-count: 1;

  -ms-animation: $keyframe $duration $transition;
  -ms-animation-iteration-count: 1;

  -o-animation: $keyframe $duration $transition;
  -o-animation-iteration-count: 1;

  transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -moz-transform: translate3d(0px, 0px, 0px);
  -ms-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate3d(0px, 0px, 0px);
}

@mixin animateMenu($duration) {
  transition: $duration cubic-bezier(0.250, 0.250, 0.750, 0.750);
  transition-property: all;
  transform: translate3d(0px, 0px, 0px);
  -webkit-transform: translate3d(0px, 0px, 0px);
  -moz-transform: translate3d(0px, 0px, 0px);
  -ms-transform: translate3d(0px, 0px, 0px);
  -o-transform: translate3d(0px, 0px, 0px);

  /*-webkit-transition: $duration cubic-bezier(0.250, 0.250, 0.750, 0.750) $property;
    -moz-transition: $duration cubic-bezier(0.250, 0.250, 0.750, 0.750) $property;
    -ms-transition: $duration cubic-bezier(0.250, 0.250, 0.750, 0.750) $property;*/
}

@mixin animateInfinite($keyframe, $duration, $transition: linear) {
  animation: $keyframe $duration $transition;
  -webkit-animation: $keyframe $duration $transition;
  -moz-animation: $keyframe $duration $transition;
  -ms-animation: $keyframe $duration $transition;
  -o-animation: $keyframe $duration $transition;
}


