.withdraw-page {

    // https://github.com/ionic-team/ionic-v1/issues/137
    ion-content{
        height: auto !important;
    }

    .withdraw-comment {
        line-height: normal;
        mix-blend-mode: normal;
    }

    .incdec-amount {
        padding: 20px 0 0;

        .amount-area {
            padding-bottom: 20px;

            .wrapper-amount {
                background-color: $withdraw-amount-bg;
                border-bottom: 1px solid $withdraw-amount-border;
                font-size: 8px;
                width: 64%;
            }

            & .amount-input-currency {
                color: $withdraw-amount-text;
            }

            input[type="number"] {
                background-color: transparent;
                color: $withdraw-amount-text;
                min-width: 140px;
            }
        }

        button.button-down,
        button.button-up,
        input[type="text"],
        input[type="number"] {
            background: transparent;
            color: $withdraw-amount-text;

            &.disable-user-behavior.activated {
                background: $withdraw-amount-bg_disabled;
            }
        }

        button.button-down,
        button.button-up {
            background: transparent;
            color: $withdraw-amount-button;

            &.disable-user-behavior.activated {
                background: $withdraw-amount-bg_disabled;
            }
        }

        button {
            width: 18%;
        }
    }

    .has-footer {
        bottom: 88px;
    }
}

