.bank-transfer {

  .bank-info {

    margin-top: 1em;

    .info-description{

      .info-title{
        font-size: 14px;
      }
      .info-value{
        font-size: 12px;
      }
    }

    .list{

      .item {
        border: 0;
        font-size: 12px;
        padding-top: 0.1em;
        padding-bottom: 0.1em;

        &:first-child{
          .info{
            border-top: 1px solid $border-main;
          }
        }

        .info {
          //border-bottom: 1px solid $border-main;
          padding-top: 0.6em;
          padding-bottom: 0.6em;
          //align-items: center;

          &.no-border {
            border: 0;
          }

          &.no-padding {
            padding: 0;
          }

          .info-value {
            text-align: right;

            .time-range {
              margin-bottom: 0.5em;
            }
          }
        }
      }

    }

  }

  ion-content {
    ion-item {
      .info-value {
        white-space: normal;
      }
    }
  }
}