.referral-activity-page {
    .page-content {
        padding: 10px 10px 0;

        .sub-head-pandats {
            padding: 10px 0;
        }

        .tabs-wrapper {

            .head-section {
                border-bottom: 1px solid $border-main;

                > .layout-column {
                    margin-bottom: -1px;
                    padding: 7px;

                    + .layout-column {
                        margin-left: 28px;
                    }

                    &.readonly {
                        color: $placeholder;
                        pointer-events: none;
                    }

                    &:not(.readonly) {
                        &.active {
                            color: $focus-color;
                            border-bottom: 3px solid $focus-color;
                        }
                    }

                    a {
                        height:  26px;
                        padding: 3px;
                    }
                }
            }


            .table-header-pandats {
                padding: 14px 10px;
            }

            ion-content.table-rows-pandats {
                position: relative;

                > .scroll {
                    width: 100%;
                }

                .rows-content {
                    > .layout-row {
                        padding: 8px 10px;

                        + .layout-row {
                            border-top: 1px solid $border-main;
                        }
                    }

                }
            }

            .qualified-area {
                .table-rows-pandats {
                    color: $state-color-success;
                }
            }

        }


        .earnings-area {
            padding-top: 10px;

            .earnings-bar {
                background: linear-gradient(180deg, lighten($state-color-success, 45%) 0%, lighten($state-color-success, 35%) 100%);
                color: $content-text;
                height: 156px;

                box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
                border-radius: 5px;
                padding: 6px;
                margin: 5px;

                position: relative;

                > .layout-row {
                    border: 1px solid $state-color-success;
                    border-radius: 5px;
                    padding: 16px;

                    .earnings-wrapper {
                        position: absolute;
                        left: 0;
                        right: 0;
                    }
                }

                .coins-stack-img {
                    width: 53px;
                    height: 67px;
                    background-image: url('/img/svg/coins-stack.svg');
                }
            }
        }

        .blank-area {
            height: 156px;
            background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='lightgrey' stroke-width='4' stroke-dasharray='6%2c 14' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
            padding: 22px 40px;
            margin-top: 10px;

            > .layout-row {
                text-align: center;
                line-height: 1.2;

                + .layout-row {
                    margin-top: 12px;
                }
            }

            .coin-dollar-img {
                width: 70px;
                height: 72px;
                background-image: url('../img/svg/coin-dollar.svg');
            }

            .double-users-img {
                width: 70px;
                height: 64px;
                background-image: url('../img/svg/double-users.svg');
            }
        }
    }

}
