    .radio-content {
        span {
            left: 35%;
        }
    }

    .custom-select:not(.has-error) {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        height: 34px;
        border: none;
        width: 100%;
        color: $content-text;
        //padding-left: 0.5em;
        //padding-right: 0.5em;
        position: relative;

        &.ng-dirty {
            position: relative;
            color: $content-text !important;

            & + i + i.ion-ios-checkmark-empty {
                display: block;
                position: absolute;
                right: 0.1em;
                top: 0;
                color: $input-success;
                font-size: 2.5em;
            }
        }
    }

    .custom-select-dropdown {
        position: relative;

        &:after {
            position: absolute;
            top: 50%;
            right: 16px;
            margin-top: -3px;
            width: 0;
            height: 0;
            border-top: 5px solid;
            border-right: 5px solid transparent;
            border-left: 5px solid transparent;
            color: #999;
            content: "";
            pointer-events: none;
        }
    }

    .has-error {

        .custom-select + i + i.ion-ios-checkmark-empty {
            display: none !important;
        }

        i.ion-ios-close-empty {
            display: block;
            position: absolute;
            right: 0.1em;
            top: 0;
            color: $input-error;
            font-size: 2.5em;
        }
    }

    .country-wrapper {
        display: flex;
        align-content: center;
        justify-content: center;
    }

    .flag-icon {
        line-height: 19px;
        width: 82px;
        margin-top: 5px;
    }

    .flag-plus {
        padding-left: 4px;
    }

    .flag-select {
    }

    .disclaimerV2enabledLink {
        margin-right: 5px;
    }

}

