.highlight-checkbox .checkbox-icon {
    border: 2px solid #ddd;
    @include animateOnce(checkboxColor, 0.8s);
}

.error-checkbox .checkbox-icon {
    border: 2px solid #EF949B;
    &:before {
        border-color: #fff;
    }
}
