#assets {
    .item-input-assets-search {
        display: flex;
        width: 100%;
        input {
            color: #fff;
            font-size: 1em;
        }
        .ion-search {
            display: flex;
            height: 100%;
            width: 1.7em;
            align-items: center;
            justify-content: center;
            font-size: 1.5em;
        }
    }

    .asset-item {
        .asset-name {
            padding-bottom: 0.4em;
            padding-top: 0.45em;
            color: $assetName-text;
        }
    }
}


.fake-button {

    .icon:before {
        font-size: 1.5em !important;
    }

    &.activated {
        color: $placeholder;
    }
}

@keyframes colorUp {
    0% {
        color: $state-color-success;
    }
    100% {
        color: $state-color-success;
    }
}

@-webkit-keyframes colorUp {
    0% {
        color: $state-color-success;
    }
    100% {
        color: $state-color-success;
    }
}

@keyframes colorDown {
    0% {
        color: $state-color-error;
    }
    100% {
        color: $state-color-error;
    }
}

@-webkit-keyframes colorDown {
    0% {
        color: $state-color-error;
    }
    100% {
        color: $state-color-error;
    }
}

.asset-item {
    .asset-price {
        color: $sub-color;
    }

    .asset-up {
        @include animateOnce(colorUp, $asset-animation-duration);

    }
    .asset-down {
        @include animateOnce(colorDown, $asset-animation-duration);
    }

    .up {
        color: $state-color-success;
    }
    .down {
        color: $state-color-error;
    }
}

