.edit-favorites-item {
    border-color: $instruments-border;
    background-color: $instruments-bg;
    color: $instruments-text;

    .item-content {
        padding-left: 0;
        -webkit-transform: translate3d(2.6em, 0, 0) !important;
        transform: translate3d(2.6em, 0, 0) !important;
        background-color: transparent;

        .title-favorites {
            line-height: 1.55em;
            color: $instruments-text;
        }
    }

    .item-left-edit {
        right: 2% !important;
        left: auto;
        z-index: 5;

        .button.icon {
            color: $placeholder;
            padding: 0;
            margin: 0;
            width: 100%;

            &:before {
                width: 100%;
                font-size: 0.9em;
            }
        }
    }

    .item-right-edit {
        padding-left: 0;
        left: 0;
        z-index: 4;
        width: 2.5em;

        .ion-navicon.button {
            min-width: 100%;
            padding: 0;
            margin: 0;
            color: $placeholder;
            z-index: 5;

            &:before {
                width: 100%;
                font-size: 0.7em;
            }
        }
    }

}