/* select */

select.transparent-select {
    width: 3em;
    height: auto;
    -webkit-appearance: none;
    border: none;
    padding: 0.1em 0;
    color: $select-transparent-text;
    text-transform: uppercase;
    background-color: transparent;
    line-height: 1em;
    /* for FF */
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: '';
    /* for IE */
    -ms-appearance: none;
    appearance: none !important;
}

select.transparent-select > option {
    background: $select-transparent-option-bg;
}

select.grey-style {
    width: 6em;
    height: auto;
    -webkit-appearance: none;
    border: 1px solid #c7c7c7;
    padding: 0.5em;
    margin: 0.1em 0 0 1.1em;
    color: #c7c7c7;
    text-transform: uppercase;
    background-color: transparent;
    background-position: right center;
    background-repeat: no-repeat;
    line-height: 1em;
    /* for FF */
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: '';
    /* for IE */
    -ms-appearance: none;
    appearance: none !important;
}

select.deposit-select {
    width: 100%;
    height: auto;
    border-radius: 2px;
    border: 1px solid #ffffff;
    background-color: #ffffff;
    padding: 0.7em 1.4em;
    margin: 0.3em 0;
}

select {
    width: 100%;
    height: 36px;
    border: 0;
    //outline: 0;
    border: none;
    margin: 0;
    //background-color: #ffffff;
    background-color: transparent;
    //margin-top: 0.2em;
    option {
        padding-left: 0;
    }
}

select.form-select {
    width: 100%;
    height: auto;
    border: 0;
    outline: 0;
    border: none;
    border-bottom: 1px solid $border-main;
    background-color: transparent;
    padding: 0.4em 0;
    margin-top: 0.2em;
    color: $content-text;

    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';

    option {
        padding-left: 0;
    }
}

select.no-appearance {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

span.select-span-form {
    z-index: 0 !important;
}

/* inputs */

input:not([type]),
input[type=text],
input[type=password],
input[type=email],
input[type=url],
input[type=time],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=tel],
input[type=number],
input[type=search],
textarea.materialize-textarea {
    display: flex;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $input-border_default;
    border-bottom: none;
    border-radius: 0;
    outline: none;
    width: 100%;
    font-size: 1rem;
    padding: 0 !important;
    box-shadow: none;
    box-sizing: content-box;
    transition: all 0.3s;
    color: $input-text;
    -webkit-user-select: auto;
    -moz-user-select: auto;
    -ms-user-select: auto;
    user-select: auto;
    -webkit-touch-callout: default;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition: background-color 50000s ease-in-out 0s;
    -webkit-text-fill-color: $content-text;
}

.input-label {
    width: 45%;
    white-space: nowrap;
}

.input-label-75 {
    width: 75%;
    white-space: nowrap;
}

input:not([type]):focus:not([readonly]) + label,
input[type=text]:focus:not([readonly]) + label,
input[type=password]:focus:not([readonly]) + label,
input[type=email]:focus:not([readonly]) + label,
input[type=url]:focus:not([readonly]) + label,
input[type=time]:focus:not([readonly]) + label,
input[type=date]:focus:not([readonly]) + label,
input[type=datetime]:focus:not([readonly]) + label,
input[type=datetime-local]:focus:not([readonly]) + label,
input[type=tel]:focus:not([readonly]) + label,
input[type=number]:focus:not([readonly]) + label,
input[type=search]:focus:not([readonly]) + label,
textarea.materialize-textarea:focus:not([readonly]) + label {
    color: $input-success;
}

input[readonly] {
    color: $input-readonly-text;
    background-color: $input-readonly-bg !important;
    border-color: $input-readonly-border;
}

.item-input, .item-select {
    .ng-not-empty + span {
        font-size: 0.8em;
        -webkit-transform: translateY(-70%);
        transform: translateY(-70%);
    }
}

.item-input {
    -webkit-user-select: auto;
    -moz-user-select: auto;
    -ms-user-select: auto;
    user-select: auto;
    -webkit-touch-callout: default;

    &.input-currency-symbol {
        input {
            padding-left: 1.2em;
        }

        &:not(.is-changed) {
            .currency-symbol {
                visibility: hidden;
            }
        }
    }

    span {
        color: $input-placeholder_default;
        position: absolute;
        padding: 0 !important;
        top: 1em;
        left: 0;
        font-size: 1em;
        cursor: text;
        transition: .2s ease-out;
        z-index: -1;
    }

    &.item-stacked-label {
        i {
            display: none;
        }
    }

    &.is-changed {
        &.input-currency-symbol {
            .currency-symbol {
                visibility: visible;
            }
        }
    }

    &:not(.has-error) {
        &.is-dirty.bottom-border:not(.is-focused) {
            border-color: $input-success;

            & span {
                color: $input-success;

                &.currency-symbol {
                    color: $input-border_default;
                }
            }
        }

        select.ng-dirty.ng-valid {
            & + span {
                color: $input-success;
            }
        }

        input.ng-dirty,
        select.ng-dirty {
            & + span + i + i.ion-ios-checkmark-empty {
                display: block;
                position: absolute;
                right: 0.1em;
                top: 0;
                color: $input-success;
                font-size: 2.5em;
            }
        }
    }
}

.as-select {
    border-bottom: 0;
    color: $placeholder;
    select {
        color: $select-text;
        background: $select-bg;
        border-bottom: 1px solid $select-border;
    }
}

.has-error {
    position: relative;

    & + .messages {
        margin-top: -0.7em;
    }

    &.bottom-border {
        border-color: $input-error;
    }

    & + .messages .help-block {
        color: $input-error;
    }

    & + .help-block {
        color: $input-error;
    }

    &.item-input.is-focused {
        border-bottom: 1px solid $input-error;
        box-shadow: 0 1px 0 0 $input-error;

        span {
            color: $input-error;

            &.currency-symbol {
                color: $input-border_default;
            }
        }
    }

    select.ng-dirty.ng-invalid {
        border-color: $input-error;
    }

    i.ion-ios-close-empty {
        display: block !important;
        position: absolute;
        right: 0;
        top: 0;
        color: $input-error;
        font-size: 2.5em;
    }
}

.input-label {
    span {
        z-index: -1;
    }
}

.is-dirty.has-error {
    i.ion-ios-checkmark-empty {
        display: none !important;
    }
}

.item-input.is-focused {
    border-bottom: 1px solid $input-border_focus;
    box-shadow: 0 1px 0 0 $input-border_focus;
}

.item-input.is-focused {
    span {
        color: $input-placeholder_focus;

        &.currency-symbol {
            color: $input-border_default;
        }
    }
}

/* buttons */
.button-full {
    background-color: $btn-fill-bg;
    color: $btn-fill-text;
    border: 2px solid transparent;
    border-radius: 4px;

    &:hover {
        background-color: $btn-fill-bg;
        color: $btn-fill-text;
        border: 2px solid transparent;
    }

    &.activated {
        background-color: $btn-fill-bg_active;
        color: $btn-fill-text_active;
        border-color: $btn-fill-border_active;
    }

    &[disabled] {

    }
}

.button-link {
    background-color: transparent;
    color: $btn-lint-text;
    border: 2px solid transparent;

    &.activated {
        color: $btn-link-text_active;
    }

    &[disabled] {

    }
}

.button-transparent {
    background-color: transparent;

    &:not(.button-white) {
        color: $btn-transparent-text;
        border: 2px solid $btn-transparent-border;

        &:hover {
            background-color: transparent;
            color: $btn-transparent-text;
            border: 2px solid $btn-transparent-border;
        }

        &.activated {
            background-color: $btn-transparent-bg_active;
            color: $btn-transparent-text_active;
            border-color: $btn-transparent-border_active;
        }
    }

    &.button-transparent-white {
        color: $btn-transparent-white-text;
        border: 1px solid $btn-transparent-white-border;

        &:hover {
            color: $btn-transparent-white-text;
            border: 2px solid $btn-transparent-white-border;
        }

        &.activated {
            background-color: transparent;
            color: $btn-transparent-white-text_active;
            border-color: $btn-transparent-white-border_active;
        }
    }

    &[disabled] {

    }
}

.button[disabled] {
    opacity: 1;
    background: $btn-disable-bg !important;
    @if variable-exists(btn-disable-text) {
        color: $btn-disable-text;
    }
}

.button-google {
    background-image: url(../img/svg/google-icon.svg);
    background-size: 20px 20px;
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: middle;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    float: left;
}

.button-facebook {
    background-image: url(../img/svg/facebook.svg);
    background-size: 20px 20px;
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: middle;
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: center;
    float: left;
}
.social {
    width: 100%;
    display: flex;
    flex-direction: row;

    .button-social:first-child {
        margin-right: 4px;
    }

    .button-social:last-child {
        margin-left: 4px;
    }
}

.button.button-social {
    width: 50%;
}

.sell-button {
    background: transparent;
    padding: 0.45em 0 0.5em;
    border-radius: 3px;
    border: 1px solid $state-color-error;
    color: $state-color-error;
    margin-bottom: 0.1em;

    div {
        color: #000;
    }

    &.activated {
        background: lighten($state-color-error, 10%);
        color: #fff;
    }
}

.buy-button {
    background: transparent;
    padding: 0.45em 0 0.5em;
    border-radius: 3px;
    border: 1px solid $state-color-success;
    color: $state-color-success;
    margin-bottom: 0.1em;

    div {
        color: #000;
    }

    &.activated {
        background: lighten($state-color-success, 10%);
        color: #fff;
        border: 0;
        outline: 0;
    }
}

.invest-buy-button {
    border-radius: 2px;
    background: $btn-invest-buy-bg;
    color: $btn-invest-buy-text;

    &.activated {
        background: lighten($btn-invest-buy-bg, 10%);
        color: $btn-invest-buy-text;
        border: 0;
        outline: 0;
    }
}

.invest-cancel-button {
    border-radius: 2px;
    background: transparent;
    border: 1px solid $btn-invest-cancel-border;
    color: $btn-invest-cancel-text;
}

.deposit-button {
    width: 5.5em !important;
    padding: 0px 5px !important;
    border-radius: 2px !important;
    text-transform: uppercase !important;
}

.fake-button {
    border: none;
    background: transparent;
    color: #fff;

    &.activated {
        background: rgba(255, 255, 255, 0.5);
    }
}

.back-button {
    min-width: 4em;

    &:active {
        background: rgba(255, 255, 255, 0.5);
    }
}

