    .tap-radio {
        div {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            flex-direction: column;
        }

        input[type=radio] {
            position: relative;
            width: 28px;
            min-width: 28px;
            height: 28px;
            display: block;
            border: 0;
            background: transparent;
            cursor: pointer;
            -webkit-appearance: none;

            &::before {
                display: table;
                width: 100%;
                height: 100%;
                border-width: 1px;
                border-style: solid;
                border-radius: 50%;
                background: $content-bg;
                content: ' ';
                transition: background-color 20ms ease-in-out;
                border-color: $placeholder;
            }

            &.ng-valid-parse:checked::before {
                border-width: 2px;
                background: $active-color;
                border-color: $active-color;
            }

            &::after {
                transition: opacity 0.05s ease-in-out;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
                position: absolute;
                top: 33%;
                left: 25%;
                display: table;
                width: 14px;
                height: 6px;
                border: 1px solid #fff;
                border-top: 0;
                border-right: 0;
                content: ' ';
                opacity: 0;
            }

            &.ng-valid-parse:checked:after {
                opacity: 1;
            }
        }

        label {
            //text-align: center;
            flex-grow: 1;
            margin-bottom: 10px;
            display: flex;
            flex-direction: row;
            align-items: center;
        }

        span {
            color: $content-text;
            padding: 4px 9px;
            width: 90%;
            text-align: left;
        }
    }

    .tap-checklist {
        input[type=checkbox] {
            position: relative;
            width: 18px;
            min-width: 18px;
            height: 18px;
            display: block;
            border: 0;
            background: transparent;
            cursor: pointer;
            -webkit-appearance: none;

            &::before {
                display: table;
                width: 100%;
                height: 100%;
                border-width: 1px;
                border-style: solid;
                border-radius: 18px;
                background: #fff;
                content: ' ';
                transition: background-color 20ms ease-in-out;
                border-color: #ddd;
            }

            &:checked::before {
                border-width: 2px;
                background: #387ef5;
                border-color: #387ef5;
            }

            &::after {
                transition: opacity 0.05s ease-in-out;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
                position: absolute;
                top: 19%;
                left: 21%;
                display: table;
                width: 10px;
                height: 6px;
                border: 1px solid #fff;
                border-top: 0;
                border-right: 0;
                content: ' ';
                opacity: 0;
            }

            &:checked:after {
                opacity: 1;
            }
        }

        label {
            display: flex;
            flex-direction: row;
            align-items: center;
        }

        span {
            font-size: 13px;
            padding: 0 4px;
            display: flex;
        }
    }

    .trading_kowledge {
        div:not(.first) {
            display: flex;
            flex-direction: column;
            padding-left: 15px;
            padding-right: 15px;

            label {
                flex: 0 0 100%;
                margin-bottom: 10px;
            }
        }

    }

    .new-checkbox {
        .item {
            white-space: normal;
        }
    }

    .has-error + .messages {
        margin-top: 0;
    }

    .has-footer {
        bottom: 87px;
    }
}



