.platform-ios.platform-cordova:not(.fullscreen) {
    & .open-trades-subheader {
        top: 64px !important;
    }

    & .order-content.order-content-subheader {
        top: 140px !important;
        &.convertUsd {
            top: 160px !important;
        }
    }
}

.opentrade-item {
    .trade-section {
        div {
            margin: 0.5em;
        }
    }

    .move-left {
        margin-left: -1.1em;
    }
}

.order-items {
    border: none;
    background: $order-content-bg;
    padding: 0.5em 0.7em 0 0.7em;

    & .tradeInfo {
        flex-flow: row wrap;

        & .tradeId {
            display: block;
            width: 100%;
        }
    }
}

.open-trades-subheader {
    flex-direction: column;
    top: 44px !important;
    background: $header-bg;

    div {
        color: $placeholder;
    }

    .accountData {
        justify-content: space-between;
        .val {
            margin-top: -5px;
        }
        .label {
            height: 25px;
            line-height: 1;
            align-items: center;
            display: flex;
            margin-bottom: 5px;
        }
        .convertusd {
            margin-top: -5px;
            color: $state-color-success;
        }
    }
}

.order-content {
    background: $content-bg;
    color: $content-text;

    &.order-content-subheader {
        top: 125px !important;
        &.convertUsd {
            top: 140px !important;
        }
    }

    &.has-subfooter.has-tabs {
        bottom: 7.52em;
    }
}

.state {
    position: relative;
    width: 100%;
}

.progress-line {
    position: absolute;
    background-color: $active-color;
    height: 5px;
    margin: 0;
    width: 40%;
    z-index: 2;
}

.progress-line-bg {
    position: absolute;
    background-color: $placeholder;
    height: 1px;
    border: 0;
    margin: 0;
    top: 2px;
    width: 100%;
    z-index: 1;
}

.color-title {
    color: $placeholder;
}

.card-ios {
    margin: 0;
    width: 100%;
    border-radius: 2px;
    background: $trade-bg;
    box-shadow: 0 1px 2px $trade-shadow;

    .col + .col{
        padding-left: 0;
    }
}

.sub-name {
    height: 1.7em;

    .status {
        color: $trade-status-name;
        margin-bottom: 0.1em;
    }
}

.symbol {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .name, .status-value {
        color: $trade-text;
    }

    .asset-name {
        color: $instruments-text;
    }
}

.state {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .trade-state {
        display: inline-block;
        padding: 0.3em;
        margin-right: 0.3em;
        border-radius: 2px;

        &.short {
            min-width: 3em;
            background: $cards-state-bg_sell;
            color: $cards-state-text;
            text-align: center;
        }

        &.buy {
            min-width: 3em;
            color: $cards-state-text;
            background: $cards-state-bg_buy;
            text-align: center;
        }
    }
}

.short-color {
    @include animateOnce(colorDown, $asset-animation-duration);
    color: $trade-text;
}

.buy-color {
    @include animateOnce(colorUp, $asset-animation-duration);
}

.border-right-order {
    border-right: 2px solid $border-main;
}

.results-section {
    flex-direction: column;
}

.profit-section {
    margin-top: 0.35em !important;
    .profitValue {
        color: $trade-text;
        &.up {
            color: $state-color-success;
        }
        &.down {
            color: $state-color-error;
        }
    }
    .profitValueUSD {
        color: $trade-text;
        opacity: 0.7;
        display: block;
        margin-top: 0;
        &.up {
            color: $state-color-success;
        }
        &.down {
            color: $state-color-error;
        }
    }
}

.opentrade-item {
    position: relative;

    .ion-android-close {
        position: absolute;
        top: 8%;
        right: 3%;
    }

    .trade-name {
        color: $trade-text;
    }

    .trade-value {
        color: $trade-text;
    }

    .border-right-order {
        border-color: $trade-border;
    }
}

.total-profit {
    border-top: 2px solid $profit-border;
    background-color: $profit-bg;
    color: $profit-text;

    &.bar-subfooter {
        bottom: 3.89em;
        height: 3.51em;
    }
    /*&.has-tabs.bar-footer.has-tabs {
        bottom: 70px;
    }*/

    .profit-title {
        padding-left: 1em;
        line-height: 1.7em;
    }
    .profit-value {
        padding-right: 1em;
        text-align: right;
        line-height: 1.7em;
        white-space: nowrap;
    }
    .profit-value-usd {
        margin-top: -7px;
    }
}
