.platform-ios.platform-cordova:not(.fullscreen) {
    .more-menu-header {
        top: 64px !important;
    }
    .more-menu-content {
        top: 155px !important;
    }
}

.more-menu-header {
    background: $header-bg;
    min-height: 90px;
    display: flex;
    flex-direction: column;
    justify-content: baseline;
    top: 44px !important;
    padding-bottom: 44px;

    .account-logo {
        text-align: center;
        font-size: 3em;
        padding-top: 0;

        div {
            border-radius: 50%;
            background: $user-logo-bg;
            width: 1.3em;
            height: 1.3em;
            margin: 0 auto;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .account-details {
        h1 {
            font-size: 2em;
            padding-bottom: 0.2em;
            margin: 0;
            color: $header-text;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
}

.more-menu-content {
    top: 135px !important;
    background-color: $menu-bg;
}

.menu-list {
    .item {
        border: none;

        .item-content {
            padding-top: 12px;
            padding-bottom: 12px;
            display: flex;
            align-items: center;
            color: $menu-text;
            background-color: $menu-bg;

            i {
                width: 1em;
                font-size: 1.4em;
                text-align: center;
                color: $menu-icon;

                + span,
                + div {
                    padding-left: 1em;
                    color: currentColor;
                }
            }

            .verification-v2 {
                display: flex;
                padding-left: 1em;
                justify-content: flex-start;
                width: 100%;
                align-items: center;
                .verificationRequered {
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    background-color: $state-color-error;
                    margin-left: 10px;
                    @include animateInfinite(blinkingBackground, 1s, infinite);
                }
            }


            &.activated {
                background: $menu-bg_active;
                color: $menu-text_active;

                i {
                    color: $menu-icon_active;
                }
            }
        }
    }

    .customLink {
        padding: 0;

        .item-content {
            padding-right: 49px;
            padding-left: 16px;
            @if variable-exists(custom_links_color) {
                background-color: $custom_links_color;
            } @else {
                background-color: $active-color;
            }
        }

        .icon-customLink {
            img {
                width: 22px;
            }
        }
    }
}

.log-out-item {
    padding-top: 12px;
    padding-bottom: 12px;
    display: flex;
    align-items: center;
    color: $menu-text;
    background-color: $menu-bg;

    i {
        width: 1.07em;
        font-size: 1.3em;
        text-align: center;
        color: $menu-icon;
    }

    span {
        padding-left: 1em;
        color: currentColor;
    }

    &.activated {
        background: $menu-bg_active;
        color: $menu-icon_active;

        i {
            color: $menu-icon_active;
        }
    }
}

@keyframes blinkingBackground{
    0%		{ opacity: 0;}
    25%		{ opacity: 0.5;}
    50%		{ opacity: 1;}
    75%		{ opacity: 0.5;}
    100%	{ opacity: 0;}
}
